  background: #E5E5E5;
  float: left;
}

button {
	width: 64px;
	height: 32px;
	border: none;
	background: #f84d44;
	color: #fff;
	font-size: 16px;
	font-weight: 700;
	border-radius: 28px;
	margin: 20px;
	padding: 4px;
